@import "../../theme.scss";

.photoalbum {
  height: 100%;
  display: flex;
  flex-direction: column;

  .photoalbum-main {
    flex: 1;
    overflow-y: auto;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
  }

  .created-modul {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.2);
    overflow: hidden;
    z-index: 99;

    .created-warp {
      width: 500px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.15);
      border-radius: 3px;

      .created-warp-header {
        display: flex;
        justify-content: space-between;
        padding: 15px 24px;
        border-bottom: 1px solid #e9e9e9;

        h3,
        span {
          height: 24px;
          font-size: 18px;
          color: rgba(0, 0, 0, 1);
          line-height: 24px;
          margin: 0;
        }

        span {
          cursor: pointer;
        }
      }

      .created-warp-boby {
        padding: 0 24px;

        .created-warp-input {
          margin: 24px 0;

          input {
            padding-left: 12px;
            width: 452px;
            height: 48px;
            background: rgba(246, 246, 246, 1);
            border-radius: 3px;
            border: 1px solid $bg-color;
            font-size: 14px;
            font-weight: 400;
            color: rgba(0, 0, 0, 1);
            line-height: 20px;
          }
        }

        .created-warp-hanle {
          text-align: right;
          padding: 22px 0;

          .cancel-btn,
          .confirm-btn {
            display: inline-block;
            padding: 0 33px;
            height: 40px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(10, 10, 10, 1);
            line-height: 40px;
            border-radius: 3px;
            border: 1px solid rgba(194, 196, 199, 1);
            cursor: pointer;
          }

          .confirm-btn {
            margin-left: 24px;
            background: $bg-color;
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }
  }
}